import React from "react";
import "hui/simple-list";

export default class HAList extends React.Component {
    static propTypes = {
        className: React.PropTypes.string,
        titleText: React.PropTypes.string,
        selectedIndex: React.PropTypes.oneOfType([React.PropTypes.string, React.PropTypes.number]),
        complexity: React.PropTypes.string,
        size: React.PropTypes.oneOfType([React.PropTypes.string, React.PropTypes.number])
    };

    constructor(props) {
        super(props);
    }

    render() {
        /* jshint ignore:start */
        return (
            <ha-list class={this.props.className} {...this.props}>
                {this.props.children}
            </ha-list>
        );
        /* jshint ignore:end */
    }
}
